import React from 'react';
import './tabs.css';

export class Tabs extends React.Component {
    render() {
        return (
          <ul>
              <li><a style={{textDecoration: 'underline', color: 'blue', cursor: 'pointer'}} id="first" onClick={this.props.onClick}>Reshatel</a></li>
              <li><a style={{textDecoration: 'underline', color: 'blue', cursor: 'pointer'}} id="second" onClick={this.props.onClick}>Trigonometrika</a></li>
              <li><a style={{textDecoration: 'underline', color: 'blue', cursor: 'pointer'}} id="third" onClick={this.props.onClick}>TicTac</a></li>
          </ul>
        );
    }
}