import React from "react";
import { Restart } from "./restart";

export class TicTac extends React.Component {
    render() {
        return (
            <div>
                <div className="container">
                    <div id="left">
                        <div onClick={this.props.onClick} id="aTic"></div>
                        <div onClick={this.props.onClick} id="dTic"></div>
                        <div onClick={this.props.onClick} id="gTic"></div>
                    </div>
                    <div id="center">
                        <div onClick={this.props.onClick} id="bTic"></div>
                        <div onClick={this.props.onClick} id="eTic"></div>
                        <div onClick={this.props.onClick} id="hTic"></div>
                    </div>
                    <div id="right">
                        <div onClick={this.props.onClick} id="cTic"></div>
                        <div onClick={this.props.onClick} id="fTic"></div>
                        <div onClick={this.props.onClick} id="iTic"></div>
                    </div>
                </div>
                <div id="restart">
                    <Restart restart={this.props.restart} />
                </div>
            </div>
        );
    }
}
