import React from 'react';
import './drob.css'

export class Trigonometrika extends React.Component {
    render() {
        return (

          <div>
              <h1>Эта хрень переведет вам радианы (в формате дроби) в градусы</h1>
              <div id="container">
                  <input id="up" type="number"/>
                  <span>π‎</span>
                  <div id="border"></div>
                  <input id="down" type="number"/>
              </div>

              <div id="equal">
                  <span>=</span>
                  <span style={{marginLeft: 5}}>s</span>
              </div>


            </div>
        );
    }
}