import React from "react";
import { TicTac } from "./ticTac";
import "./ticTac.css";

export class TicTacContainer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            player: "X",
            isPlaying: true
        };
        this.handleClick = this.handleClick.bind(this);
        this.handleRestart = this.handleRestart.bind(this);
    }
    handleClick(e) {
        if (this.state.isPlaying === true) {
            let id = e.target.id;

            if (e.target.className !== "activated") {
                document.getElementById(id).innerHTML = this.state.player;
                if (this.state.player === "X") {
                    this.setState({ player: "O" });
                } else {
                    this.setState({ player: "X" });
                }
            }

            e.target.classList.add("activated");
            this.winChecker();
        }
    }
    handleRestart() {
        const squares = ["aTic", "bTic", "cTic", "dTic", "eTic", "fTic", "gTic", "hTic", "iTic"];
        for (let element of squares) {
            document.getElementById(element).innerHTML = "";
            document.getElementById(element).classList.remove("activated");
            this.setState({
                isPlaying: true,
                player: "X"
            });
        }
    }
    winChecker() {
        const winningConditions = [
            ["aTic", "bTic", "cTic"],
            ["dTic", "eTic", "fTic"],
            ["gTic", "hTic", "iTic"],
            ["aTic", "dTic", "gTic"],
            ["bTic", "eTic", "hTic"],
            ["cTic", "fTic", "iTic"],
            ["aTic", "eTic", "iTic"],
            ["cTic", "eTic", "gTic"]
        ];
        for (let i = 0; i < 8; i++) {
            let condition = winningConditions[i];
            let content =
                document.getElementById(condition[0]).innerText +
                document.getElementById(condition[1]).innerText +
                document.getElementById(condition[2]).innerText;
            if (content === "XXX" || content === "OOO") {
                this.setState({ isPlaying: false });
                break;
            }
        }
    }
    render() {
        let ticTac;
        if (this.props.render) {
            ticTac = <TicTac onClick={this.handleClick} isPlaying={this.state.isPlaying} restart={this.handleRestart} />
        } else {
            ticTac = null
        }
        return (
            <div>
                {ticTac}
            </div>
        );
    }
}
