import React from 'react';
import { Tabs } from './tabs';
import { SolverContainer } from '../solver/SolverContainer';
import { TrigonometrikaContainer } from "../trigonometrika/TrigonometrikaContainer";
import { TicTacContainer } from "../tictactoe/ticTacContainer";

export class TabsContainer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            first: true,
            second: false,
            third: false,
            fourth: false
        }
        this.handleClick = this.handleClick.bind(this)
    }
    handleClick(e) {
        let id = e.target.id;
        if (!this.state[id]) {
            this.setState( {
                [id]: !this.state[id] //активирует true на кликнутом элементе (в this.state)
            })
            let array = Object.keys(this.state);
            let index = array.indexOf(id);
            array.splice(index, 1);
            for (let i = 0; i <= array.length; i++) {
                if (this.state[array[i]] === true) {
                    this.setState( {
                        [array[i]]: false //меняет на false все что не есть активный элемент
                    })
                }


            }

        }
        e.preventDefault();
    }
    render() {
        return (
            <div>
                <Tabs onClick={this.handleClick} />
                <SolverContainer render={this.state.first} />
                <TrigonometrikaContainer render={this.state.second} />
                <TicTacContainer render={this.state.third} />
            </div>


        );
    }
}